import PropTypes from "prop-types";
import { useState } from "react";
import UploadMultipleShowFile from "./common/upload-multiple-show-file";
import { UPLOAD_FILE_NAME_PREFIX } from "../../../constants/prefix";
import UploadDropzoneMultiple from "./common/upload-dropzone-multiple";
import i18n from "../../../utils/locale/i18n";

function InputUploadMultiple({
  fieldOptions,
  name,
  register,
  setValue,
  value,
}) {
  const [files, setFiles] = useState(value ? value.split(",") : []);
  const [deleteFile, setDeleteFile] = useState(false);

  const onDelete = (index) => {
    const filteredFiles = files.filter((_, i) => i !== index);
    setFiles(filteredFiles);
    setValue(name, filteredFiles.join(","));
    setDeleteFile(true);
  };

  const undoDelete = () => {
    setDeleteFile(false);
    setValue(name, value);
    setFiles(value.split(","));
  };

  const handleFilesChange = (files) => {
    setValue(`${name}${UPLOAD_FILE_NAME_PREFIX}`, files);
  };

  return (
    <>
      <div>
        <UploadDropzoneMultiple onFilesChange={handleFilesChange} />
        <input
          type="hidden"
          value={files.join(",")}
          name={name}
          {...register(name)}
        />
      </div>
      {files.length > 0 && (
        <div>
          {files.map((file, index) => {
            return (
              <UploadMultipleShowFile
                {...{
                  onDelete: () => onDelete(index),
                  fieldOptions,
                  value: file,
                  name: `${name}[${index}]`,
                  key: index,
                }}
              />
            );
          })}
        </div>
      )}
      {deleteFile && (
        <button
          type="button"
          className="btn btn-default btn-outline-dark"
          onClick={undoDelete}
        >
          <i className="fas fa-undo"></i>&nbsp;{i18n.t("restore_deleted_files")}
        </button>
      )}
    </>
  );
}

InputUploadMultiple.propTypes = {
  className: PropTypes.string,
  control: PropTypes.object,
  id: PropTypes.string,
  name: PropTypes.string,
  onBlur: PropTypes.func,
  onChange: PropTypes.func,
  required: PropTypes.bool,
  value: PropTypes.string,
};

export default InputUploadMultiple;
